@import url('https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,200..800;1,200..800&display=swap');
*{
  margin: 0;
  box-sizing: border-box;
  font-family: Karla, sans-serif;
}

/* NAVBAR */
.Navbar--top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px 20px 30px;
  color: white;
  background: linear-gradient(to right, #672280, #A626D3);
}
.Navbar--first{
  display: flex;
  align-items: center;
  gap: 10px;
}
.Troll-img{
  width: 50px;
  height: 44px;
}
.Navbar-text{
  font-size: 1.5rem;
  font-weight: bold;
}
.Navbar--para{
  font-size: 1rem;
  font-weight: normal;
}

/* FORM */

.Form--main{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px;
}
.Input--div{
  display: flex;
  justify-content: space-between;
}
input{
  width: 40vw;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
}
label{
  font-size: 1.2rem;
  font-weight: 500;
}
button{
  margin-top: 10px;
  padding: 15px;
  border-radius: 5px;
  border: none;
  background: linear-gradient(to right, #672280, #A626D3);
  color: white;
  font-size: 1rem;
  font-weight: bold;
}
button:hover{
  cursor: pointer;
  background-color: #672280;
  background-image: none;
}
.img--generated{
  width: 50vw;
  margin: auto;
  padding: 30px 0 0 0;
}
.meme--img{
  display: flex;
  position: relative;
  justify-content: center;
}
.head--1{
  position: absolute;
  top: 30px;
  color: white;
  font-size: 3rem;
    color: white;
      /* Text color */
      text-shadow:
        -1.5px -1.5px 0 #000,
        1.5px -1.5px 0 #000,
        -1.5px 1.5px 0 #000,
        1.5px 1.5px 0 #000;
}
.head--2{
  position: absolute;
  bottom: 10px;
  color: white;
  font-size: 3rem;
      text-shadow:
        -1.5px -1.5px 0 #000,
        1.5px -1.5px 0 #000,
        -1.5px 1.5px 0 #000,
        1.5px 1.5px 0 #000;

}